@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200;300;400;500;700&family=Roboto:wght@100;300;400;500;700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #F2F5FA;
}

* {
  box-sizing : border-box;
}

.container {
  margin: 0 auto;
  padding: 5em 3em;
  max-width: 900px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.head {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

p {
  margin: 0;
}
/* =======
header
========== */

header {
  margin: 0;
  width: 50%;
  /* border: aqua 1px solid; */
}

h1{
  font-size: 3em;
  margin: 0;
}

.subtile {
  margin: 0;
  font-size: 1rem;
  font-weight: 400;
}

span{
  transition-duration: .3s;
}

span:hover,
span:focus {
  color:#72A7FF;
  transition-duration: .3s;
}

/* ========
result
=========== */

.result {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin: 0;
  -webkit-align-self: center;
          align-self: center;
}

.time,
.words,
.chars {
  font-family: Roboto,sans-serif;
  font-weight: thin;
  margin: 0 1em;
  text-align: center;
}

.num {
  padding-bottom: .1em;
  font-weight: 600;
  font-size: 2rem;
}

svg{
  color:#417EE3;
}

.time {
  padding: 1.1em 1.13em;
  border: solid 4px #72A7FF;
  border-radius: 50px;
}

.words,
.chars {
  -webkit-align-self: center;
          align-self: center;
}

.num-result {
  font-size: 3rem;
  padding: 0 .4em;
}

.font-result{
  padding-top: .5em;
  font-size: .5rem;
}

.border {
  border-bottom: solid 2px black;
  padding-bottom: .2em;
}

/*=========
Typing area
=========== */

.article{
  font-weight: 300;
  width: 95%;
  margin: 3.5em 0 1em 0;
  -webkit-align-self: center;
          align-self: center;
}

textarea {
  width: 100%;
  height: 10rem;
  padding: 1.3em;
  margin: 1.5em 0;
  -webkit-align-self: center;
          align-self: center;
  background: #FFFFFF;
  border: 1px solid #F2F5FA;
  box-sizing: border-box;
  box-shadow: 0px 4px 59px #F6F6F7;
  border-radius: 14px;
  outline: none;
  resize: none;
  line-height: 1.6em;
}

textarea:disabled{
  background-color: #dfe6f2;
}

button {
  -webkit-align-self: center;
          align-self: center;
  font-size: 1rem;
  color: #F2F5FA;
  width: 8rem;
  padding: .5em;
  background: #417EE3;
  border: none;
  border-radius: 71px;
  transition-duration: .3s;
  outline: none;
}

button:hover,
button:focus {
  box-shadow: 0px 4px 20px rgba(65, 126, 227, 0.43);
}

button:disabled{
  background: #8fa7cf;
  box-shadow: none;
}

@media screen and (max-width: 700px){
  
  .container{
    padding: 5em 1em;
  }

  .head{
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }

  
  header {
    margin: 0;
    width: 100%;
    text-align: center;
  }

  h1{
    margin-bottom: 1em;
  }

  textarea{
    height: 15rem;
  }
}
